<template>
    <div class="simon-container">
        <h1 class="simon-title">Simon Game</h1>
        <div class="game-field">
            <div @click="getPlayerClicks" class="game-field__color game-field__color_yellow"></div>
            <div @click="getPlayerClicks" class="game-field__color game-field__color_blue"></div>
            <div @click="getPlayerClicks" class="game-field__color game-field__color_red"></div>
            <div @click="getPlayerClicks" class="game-field__color game-field__color_green"></div>
        </div>
        <div class="game-panel">
            <button class="game-panel__btn" @click="startGame"
                    v-bind:disabled="gameState.isPlaying">START</button>
            <div class="game-panel__info">Раунд: <span class="game-panel__round">{{roundState.order.length}}</span></div>
            <div class="game-panel__info">Рекорд: <span class="game-panel__record">{{gameState.maxScore}}</span></div>
        </div>
    </div>
</template>

<script>
var yellow_field, blue_field, red_field, green_field, fields_arr;
var currentGameState = null;


    export default {
        name: 'simonGame',
        data() {
            return {
                gameState: {
                    currentRoundNum: 1,
                    maxScore: 0,
                    currentRoundState : null,
                    isLocked: false,
                    isPlaying: false,
                },
                roundState: {
                    score: 0,
                    index: 0,
                    order: []
                }
            }
        },

        methods: {
            randomInt(min, max) {
                return min + Math.floor((max - min) * Math.random());
            },
            getNewOrder(count) {
                let result = [];
                for (let i=0; i<count; i++){
                    result[i] = this.randomInt(0, 3);
                }
                return result;
            },
            showOrder(order) {
                console.log(order)
               function myLoop(i) {
                currentGameState.isLocked = true;
                   fields_arr.forEach((x) => {x.classList.remove('clicked')})
                   fields_arr[order[i]].classList.add('clicked')
                    setTimeout(function() {
                        fields_arr[order[i]].classList.remove('clicked')
                        setTimeout(()=>{
                            i++;
                            if (i < order.length) {
                                myLoop(i);
                            }
                        },200)
                        currentGameState.isLocked = false;

                    }, 800)
                }
                myLoop(0)
            },
            startGame() {
                yellow_field = document.querySelector('.game-field__color_yellow');
                blue_field = document.querySelector('.game-field__color_blue');
                red_field = document.querySelector('.game-field__color_red');
                green_field = document.querySelector('.game-field__color_green');
                fields_arr = [yellow_field, blue_field, red_field, green_field];

                if(currentGameState === null){
                    currentGameState = this.gameState;
                    currentGameState.currentRoundState = this.roundState;
                }
                this.gameState.isPlaying = true;
                currentGameState.currentRoundState.order = this.getNewOrder(1);
                currentGameState.currentRoundState.index = 0;
                this.showOrder(currentGameState.currentRoundState.order)
            },
            getPlayerClicks(e) {
                if(currentGameState.isLocked || !this.gameState.isPlaying){
                    return
                }
                let indexOfClickedField = fields_arr.indexOf(e.target);
                this.showOrder([indexOfClickedField])
                this.checkIfUserClickedCorrectly(indexOfClickedField)
            },
            checkIfUserClickedCorrectly(clickedValue) {
                let round = currentGameState.currentRoundState
                if(clickedValue === round.order[round.index]){
                    round.index++;
                    if(round.index>=round.order.length){
                        console.log("WIN");
                        round.order = this.getNewOrder(round.order.length+1);
                        round.index = 0;
                        this.gameState.maxScore = round.order.length-1;
                        setTimeout(()=>this.showOrder(round.order), 2000)
                    }
                }else{
                    console.log("LOOSE");
                    currentGameState.isLocked = false;
                    this.gameState.isPlaying = false;
                    round.order = [];
                    alert('Вы проиграли :(')
                }

            }
        }

    }
</script>
<style lang="sass">
    .simon-title
        text-align: center
        width: 100%
    .simon-container
        display: flex
        flex-wrap: wrap
        padding: 0 30px
        max-width: 800px
        margin: 0 auto
        @media screen and (max-width: 595px)
            padding: 0 15px
    .game-panel
        justify-content: center
        flex-direction: column
        display: flex
        &__info
            font-size: 1.6rem
            font-weight: 500
            margin-bottom: 20px
            span
                font-weight: 700
                font-size: 1.8rem
        &__btn
            margin-bottom: 45px
            background-color: #77b55a
            border-radius: 8px
            border: 2px solid #4b8f29
            display: inline-block
            cursor: pointer
            color: #ffffff
            font-family: Verdana
            font-size: 22px
            font-weight: bold
            padding: 20px 46px
            text-decoration: none
            &:hover
                background-color: #5b9241
            &:active
                position: relative
                top: 1px


    .game-field
        display: flex
        flex-wrap: wrap
        margin: 20px auto
        max-width: 500px
        width: 100%
        height: 500px
        cursor: pointer
        @media screen and (max-width: 565px)
            width: 200px
            height: 100px

        &__color
            max-width: 250px
            width: 100%
            height: 250px
            @media screen and (max-width: 565px)
              width: 100px
              height: 100px
            /*opacity: .8*/
            &_yellow
                background-color: #f8c212
                border-radius: 30% 0 0 0
            &_blue
                background-color: blue
                border-radius: 0 30% 0 0
            &_green
                background-color: green
                border-radius: 0 0 30% 0
            &_red
                background-color: crimson
                border-radius: 0 0 0 30%
    .clicked
      opacity: .5

</style>
